.sermon{
  display: contents;
  align-items: center;
  justify-content: center;

  width: calc(100%);
  height: calc(100%);

  .bottom-nav {
      display: block;
            height: 58px;
            box-shadow: 0 -2px 5px -2px #333;
            background-color:black;
            position: fixed;
            bottom: 0px;
            left: 0px;
            width: 100%;
            z-index: 3;
      .sticky-footer {
            display: block;
            height: 58px;
            box-shadow: 0 -2px 5px -2px #333;
            background-color:black;
            position: fixed;
            bottom: 0px;
            left: 0px;
            width: 100%;
            z-index: 3;
      }
      .sticky-footer .one-fourth {
            width: 50%;
            float: left;
            color: #fff;
            text-align: center;
            height: 58px;
            position: relative;
            cursor: pointer;
      }
      .sticky-search,
      .sticky-filters,
      .sticky-map,
      .sticky-radius {
            width: 25px;
            height: 25px;
            margin: auto;
            margin-top: 11px;
      }
      .sticky-footer .one-fourth p {
            font-size: 10px;
            margin-top: 3px;
      }
      
      }

      .htNdhd {
            position: relative;
            margin: 0 auto;
            border-radius: 5px;
            box-shadow: 0px 0px 15px -5px rgba(0,0,0,0.75);
      }

      .kHWrPi {
            border-radius: 5px 5px 0 0;
            height: 240px;
            padding: 2rem;
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            -webkit-flex-wrap: wrap;
            -ms-flex-wrap: wrap;
            flex-wrap: wrap;
            color: #27117c;
            box-sizing: border-box;
            background-image: linear-gradient( to bottom, #ebe6f8, #ece5f6, #ede4f5, #eee4f3, #efe3f1 );
      }

      .cgLFdP {
            height: 260px;
            box-sizing: border-box;
            overflow: scroll;
      }

      .gkcZCz {
            /* width: 100%; */
            height: 45px;
            cursor: pointer;
            color: #b2b2b2;
            margin-left: 2rem;
            margin-top: 1rem;
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            box-sizing: border-box;
      }

      //////////@extend
      $finalHeight: 250px;
      $finalWidth: 3 * $finalHeight;
      $scrollBarHeight: 1px;
      
      ::-webkit-scrollbar {
        width: $scrollBarHeight;
        height: $scrollBarHeight;
      }
      
      ::-webkit-scrollbar-button {
        width: $scrollBarHeight;
        height: $scrollBarHeight;
      }
      
  
      div {
        box-sizing: border-box;
      }
      
      .horizontal-scroll-wrapper {
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        width: calc(#{$finalHeight} + #{$scrollBarHeight});
        max-height: $finalWidth;
        margin: 0;
        padding-top: $scrollBarHeight;
        background: #abc;
        overflow-y: auto;
        overflow-x: hidden;
        transform: rotate(-90deg) translateY(-$finalHeight);
        transform-origin: right top;
        & > div {
          display: block;
          padding: 5px;
          background: #cab;
          transform: rotate(90deg);
          transform-origin: right top;
        }
      }
      
      .squares {
        padding: $finalHeight 0 0 0;
        & > div {
          width: $finalHeight;
          height: $finalHeight;
          margin: 10px 0;
        }
      }
}