      .content{
        width: 100%;
        max-width: calc(100%);
        margin: auto;
        padding-top: 50px;
        display: flex;
        overflow: scroll;
//     align-items: center;
//     justify-content: center;
    
//     width: calc(100% - 10px);
      height: 580px; //50em;//calc(100% - 15px);
      }
      
      .title{
          left: 145px;
          top: 25px;
      }
      
      .list{
        width: 100%;
        margin:auto;
        margin-bottom: 65px;
      }
      
      
      .list .btn-flat {
          float: right;
          width: 150px;
          padding: 0 10px;
          margin-top: 40px;
      }
      
      
      #sortable { 
          list-style-type: none; 
          margin: auto; 
          padding: 0;
          width: 100%; 
          cursor: move;
      }
      
      #sortable li {  
          padding: 0.4em;  
          font-size: 1.4em; 
         // height: 326px;
         // width: 60%;
          margin: auto;
          height: 700px;
          margin-bottom: 45px;
      }
      
      
      #sortable li span { 
          position: absolute; 
          color: #555; 
          font-weight: 500;
          top: 265px;
          left: 20px;
      }
      
      
      #sortable .video-container iframe, .video-container object, .video-container embed {
          width: 100%;
          height: 580px;
          display: flex;
          visibility: visible;
      }
      
      #sortable .video-container {
          position: relative;
          width: 100%;
          height: 235px;
          padding-bottom: 0;
          height: 580px;
      }
      
      
      #sortable p{
          font-size: 0.8rem;
          color: #555;
          font-weight:500;
          top: 284px;
          padding-left: 12px;
          position: absolute;
      }
      
      .collection{
          border: none;
      }
      
      .collection .collection-item {
          background-color: #fff;
          margin-bottom: 45px;
      }
      
      
      
      /* heart button */
      .favorite-heart {
          position: absolute;
          bottom: 26px;
          right: 20px;
          cursor: pointer;
      }
      
      .unlike{
        color:#ccc;
      }
      
      .like{
        color:#f00650;
      }
      
  
