.registration{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 15px;
    margin: 0 auto;
    max-width: 600px;
    background: aliceblue;
    height: 661px;

    .table{
      width: calc(100%);
    }

    @media all and ( height: 661px){
        height: 661px;  
    }

    .btn {
      background-color: #55BD91;
      color: #fff;
      display: inline-block;
      border-style: none;
      border-radius: 10px;
      background: #55bd91;
      box-shadow: none;
      text-shadow: none;
      color: #ffffff;
      border-width: 0;
      border-color: 0;
      font-size: 18px;
      line-height: 22px;
      /* padding: 20px 35px; */
      font-weight: 600;
      margin-top: 15px;
  }
    
    .ghost-black {
        color: black !important;
        border-color: black;
    }
    
    .ghost-black:hover {
        color: white !important;
        background-color: black;
    }
    
    
    /* BANK-ICON-CONTAINER */
    [class*="bank-icon-"] {
        display: inline-block;
        background-image: url("https://www.payfast.co.za/images/plogos.png");
        background-repeat: no-repeat;
        background-position-y: bottom;
        height: 32px;
        float: lzeft;
        margin-right: 10px;
        border: 1px solid red;
    }
    
    /* [class*="bank-icon-"]:hover {
      background-position-y: 0%;
    }
     */
    /* BANK - ICONS */
    .bank-icon-absa { 
      background-position-x: -6px;     
      width: 28px; 
    }
    
    .bank-icon-fnb { 
      background-position-x: -46px; 
      width: 21px; 
    }
    .bank-icon-nedbank { 
      background-position-x: -79px; 
      width: 20px; 
    }
    .bank-icon-stdbank { 
      background-position-x: -111px; 
      width: 16px; 
    }
    .bank-icon-visa { 
      background-position-x: -191px; 
      width:  48px 
    }
    .bank-icon-mcard { 
      background-position-x: -145px;
      width: 34px; 
    }   
    
    .currency-symbol {
      position: relative;
      margin-left: 30px;  
    }
    
    .currency-symbol:before {
      position: absolute;
      content: 'R';
      top: 0;
      left: 0;
      width: 20px;
      height: 20px;
      border: 1px solid green;
      
    }
    
    .thankyou{

    }

    h1,
    .text-secondary,
    h2 {
      color: rgb(72, 90, 104);
    }

    h2 {
      font-weight: 500;
      font-style: normal;
      font-size: 36px;
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6,
    .font-secondary {
      font-family: 'Quicksand', sans-serif;
    }

    .bg-secondary {
      background-color: rgba(246, 251, 249, 1);
    }

    .btn {
      background-color: #55BD91;
      color: #fff;
      display: inline-block;
      border-style: none;
      border-radius: 10px;
      background: rgba(85, 189, 145, 1);
      box-shadow: none;
      text-shadow: none;
      color: #ffffff;
      border-width: 0;
      border-color: 0;
      font-size: 18px;
      line-height: 22px;
      padding: 20px 35px;
      font-weight: 600;
      margin-top: 15px;
    }

    h1 {
      font-size: 64px;
      font-weight: 500;
      font-style: normal;
    }

    table,
    .margin-center {
      margin: 0 auto;
    }

    .container {
      padding: 0 15px;
      margin: 0 auto;
      max-width: 600px;
    }

    input:focus,
    textarea:focus,
    select:focus {
      border-color: rgba(85, 189, 145, 1);
      outline: 0;
      box-shadow: none;
    }

    label {
      display: block;
      margin: 5px 0 5px;
      font-weight: 500;
    }

    input,
    textarea,
    select {
      -moz-appearance: none;
      -webkit-appearance: none;
      appearance: none;
      border: 1px solid #7c8d9b;
      background: none;
      border-radius: 10px;
      padding: 15px;
      margin-bottom: 15px;
      width: 100%;
      display: block;
      height: auto;
      font-size: 14px;
      color: #7c8d9b;
      font-weight: 400;
      font-family: 'Quicksand', sans-serif;
    }

    select {
      padding-right: 35px;
    }

    .select {
      position: relative;
      width: 100%;
    }

    .select:after {
      position: absolute;
      content: "";
      top: 21px;
      right: 15px;
      width: 0;
      height: 0;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-top: 8px solid rgba(85, 189, 145, 1);
      z-index: -1;
    }
}