.events-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 75px 10px 20px 10px;

    width: calc(100%);
    height: calc(100% - 55px);

    .MuiPaper-root {
        color: white;
        transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
        background-color: cadetblue;
    }

    .Cell-text-245 {
        padding: 0.5em;
        font-size: medium;
        font-family: cursive;
      }
}
