  .main {
    width: calc(100%);
    height: calc(100%);
    height: 700px;
    position: relative;
    background: #fff;
    // border-radius: var(--ham-size);
    // border: 15px solid black;
    overflow: hidden;

    
    #root {
      @media all and (max-width: 360px) and (height: 640px) {
        display: inline-grid;
        height: 640px;
      }

      @media all and (width: 411px) and (height: 731px) {
        display: inline-grid;
        height: 731px;
      }

      @media all and (width: 411px) and (height: 823px) {
        display: inline-grid;
        height: 823px;
      }

      @media all and (width: 320px) and (height: 568px) {
        display: inline-grid;
        height: 568px;
      }

      @media all and (width: 375px) and (height: 812px) {
        display: inline-grid;
        height: 812px;
      }

      @media all and (width: 375px) and (height: 667px) {
        height: 667px;
      }

      @media all and (width: 414px) and (height: 736px) {
        display: inline-grid;
        height: 736px;
      }

      @media all and (width: 375px) and (height: 667px) {
        display: inline-grid;
        height: 667px;
      }
    }
  }

  .menu-wrap {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100%;
  }

  /* Checkbox toggle */
  .toggle {
    position: absolute;
    bottom: var(--ham-pos-y);
    right: var(--ham-pos-x);
    z-index: 2;
    width: var(--ham-size);
    height: var(--ham-size);
    opacity: 0;
    cursor: pointer;
  }

  .hamburger-wrap {
    position: absolute;
    bottom: var(--ham-pos-y);
    right: var(--ham-pos-x);
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: var(--ham-size);
    height: var(--ham-size);
    padding: 1rem;
    border-radius: 50%;
    box-shadow: 0 0.1px 1.1px -12px rgba(0, 0, 0, 0.02),
      0 0.3px 2.7px -12px rgba(0, 0, 0, 0.028),
      0 0.6px 5px -12px rgba(0, 0, 0, 0.035),
      0 1.3px 8.9px -12px rgba(0, 0, 0, 0.042),
      0 2.5px 16.7px -12px rgba(0, 0, 0, 0.05),
      0 6px 40px -12px rgba(0, 0, 0, 0.07);
    background: var(--primary-color);
  }

  /* Hamburger icon */
  .hamburger {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 2px;
    background: #fff;
    transition: all 0.4s ease;
  }

  .hamburger::before,
  .hamburger::after {
    content: "";
    position: absolute;
    top: -5px;
    z-index: 1;
    width: 100%;
    height: 2px;
    background: inherit;
  }

  .hamburger::after {
    top: 5px;
  }

  /* Animating hamburger icon */
  .toggle:checked + .hamburger-wrap > .hamburger {
    transform: rotate(135deg);
  }

  .toggle:checked + .hamburger-wrap > .hamburger:before,
  .toggle:checked + .hamburger-wrap > .hamburger:after {
    top: 0;
    transform: rotate(90deg);
  }

  .toggle:checked:hover + .hamburger-wrap > .hamburger {
    transform: rotate(225deg);
  }

  /* Slide menu */
  .menu {
    position: relative;
    height: 100%;
  }

  .menu::after {
    content: "";
    top: 0;
    left: 0;
    display: inline-block;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 0;
    transition: opacity 200ms ease-in;
  }

  .menu > ul {
    position: absolute;
    bottom: 0;
    margin: 0;
    width: 100%;
    padding: 2rem 1rem 5rem;
  }

  .menu > ul > li {
    list-style: none;
  }

  .menu > ul > li a {
    display: inline-block;
    width: 100%;
    padding: 1rem;
    color: inherit;
    text-decoration: none;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  }

  /* Animating menu */
  .toggle:checked ~ .menu {
    transform: translateY(0%);
  }

  .toggle:checked ~ .menu::after {
    opacity: 1;
  }

  .toggle ~ .menu > ul > li a {
    color: #fff;
    opacity: 0;
    transform: translateY(100%);
    transition: all 0.2s ease-out;
  }

  .toggle:checked ~ .menu > ul > li a {
    opacity: 1;
    transform: translateY(0);
    transition-delay: calc(0.075s * var(--index));
  }

  /* Content */
  .content {
    //padding: 5rem 2rem;
    height: 100%;
  }

  .content h1 {
    font-size: 4rem;
    margin: 1rem 0;
    font-weight: 700;
    color: #2c32be;
  }


  
